import React, { useState, useEffect } from 'react';
import Lottie from 'react-lottie';
import { strings } from 'assets/data/data';
import styles from './UnderConstruction.module.scss';

// Create an array with the filenames of the animation data
const animationDataFilenames = [
  '1.json',
  '2.json',
  '3.json',
  '4.json',
  '5.json',
  '6.json',
  '7.json',
  '8.json',
  '9.json',
  '10.json',
  '11.json',
  '12.json',
  '13.json',
  '14.json',
  '15.json',
  '16.json',
  '17.json'
];

const UnderConstruction = () => {
  const [currentAnimationData, setCurrentAnimationData] = useState(null);

  // Function to select a random animation data
  const selectRandomAnimation = () => {
    const randomIndex = Math.floor(Math.random() * animationDataFilenames.length);
    const filename = animationDataFilenames[randomIndex];
    // Use require to dynamically import the animation data based on the filename
    const animationData = require(`assets/lotties/under-construction/${filename}`);
    setCurrentAnimationData(animationData);
  };

  // Call selectRandomAnimation on component mount to set the initial animation data
  useEffect(() => {
    selectRandomAnimation();
  }, []);

  const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: currentAnimationData,
    rendererSettings: {
      preserveAspectRatio: 'xMidYMid slice',
    },
  };

  return (
    <div className={styles.wrapper} >
      {currentAnimationData && (
        <Lottie options={defaultOptions} height="50%" width="50%" />
      )}
      {strings.underConstruction}
    </div>
  );
};

export default UnderConstruction;
